import React from "react";
import Layout from "../layout/layout";
import VisaContact from "./visacontact";
import studenticon from "../../assets/images/visa/student-icon.svg";
import student from "../../assets/images/visa/student.png";
import "./visa.css";



const StudentVisa = () => {




  
    return (

    <Layout>


        <div className="express-visa-page">


            <div className="express-visa-banner">

                <div className="container">

                    <div className="express-visa-banner-heading">

                        <img src={studenticon} alt="" />
                        <div className="express-visa-banner-heading-title">
                            <h5>Temporary</h5>
                            <h2>Student Visa - Higher Education in Canada’s Universities</h2>
                        </div>

                    </div>

                    <p>Each year Canada issues more than 130,000 Student Visas for foreign nationals to study at its world-renowned centers of higher education.</p>

                    <p>In a recent study by the Times Higher Education World University Rankings, out of 1,000 universities, Canada had 4 universities ranked in the top 100, 10 in the top 250, and 19 in the top 500. Also, Canadian universities have some of the lowest tuition fee rates in the world (starting as low as CAN $6,000 per year).</p>

                    <p>According to the International Student Survey conducted by the Canadian Bureau for International Education, over 90% of those in higher education were satisfied or very satisfied with their studies in Canada, and 95% would recommend Canada as a place to study.</p>

                    <p>If you wish to join them, we at UIS can help you apply for a Visa that allows international students to stay in Canada while pursuing a specific course or program at an approved institute of higher education.</p>

                    <p>Upon graduating, international students may apply for a work permit under the Post-Graduate Work Permit Program.</p>


                </div>

            </div>





            <div className="express-program student-program">

                <div className="container">

                    <div className="express-program-grid">

                        <div className="express-program-content">

                            <h3>Eligibility</h3>
                            <h4>To be eligible foreign students wishing to study in Canada need to fulfill the following criteria:</h4>

                            <ul>
                                <li>They must have already been accepted to study at an approved Canadian institution.</li>
                                <li>They must prove that they have sufficient funds to pay for tuition fees, living expenses, and return transport after the completion of their study program.</li>
                                <li>They must provide proof (a valid police certificate) stating that they are law-abiding citizens with no criminal record and that therefore they are not a security risk to Canada.</li>
                                <li>They must be in good health and be willing to undertake a medical examination should that be deemed to be necessary.</li>
                                <li>They must satisfy immigration authorities that they will indeed leave Canada upon completion of the study program and their authorized stay.</li>
                            </ul>

                            <p>In some cases, a Student Visa is not required. This is true if the course program lasts less than two months; if you are a family member or staff of a foreign representative to Canada; if you are a member of a foreign armed force.</p>

                            <p><b>A single eligibility evaluation starts at $990. A family eligibility evaluation starts at $1490.</b></p>

                        </div>

                        <div className="express-program-cover">
                            <img src={student} alt="" />
                        </div>

                    </div>
                </div>
            </div>


            <VisaContact />

           
        </div>

    </Layout>

    );
  }
  
export default StudentVisa;