import React from "react";
import Layout from "../layout/layout";
import VisaContact from "./visacontact";
import touristicon from "../../assets/images/visa/tourist-icon.svg";
import tourist from "../../assets/images/visa/tourist.png";
import "./visa.css";



const TouristVisa = () => {




  
    return (

    <Layout>


        <div className="worker-visa-page">


            <div className="worker-visa-banner">

                <div className="container">

                    <div className="worker-visa-banner-heading">

                        <img src={touristicon} alt="" />
                        <div className="worker-visa-banner-heading-title">
                            <h5>Temporary</h5>
                            <h2>Visitors/Tourist Visa</h2>
                        </div>

                    </div>

                    <p>A Visitor or Tourist Visa enables you to enter Canada on a temporary basis for a period of up to 6 months, for the purpose of a holiday, work, or family visit. You must have a valid Visitor Visa before you enter Canada unless arriving by air from visa-exempt countries. In such cases, you are required to have a valid Electronic Travel Authorisation (ETA) before entering the country. Your Visitor Visa may be issued to cover single or multiple entries to Canada.</p>



                    <div className="worker-grid">

                        <div className="worker-content">

                            <p>The single entry visa is valid for 6 months and may be used on just one occasion. Therefore if you leave the country – even before the end of the 6 month period – you will need to apply for a new visa before you can re-enter Canada.</p>

                            <p>A Multiple Entry visa is issued valid for a period of up to 10 years. This may be used for re-entry up to 1 month before your passport or Multiple Entry visa expires (whichever is the earlier).</p>

                            <p>If you wish to apply for a Visitor or Tourist Visa, we will assign you a personal account manager who will explain to you all the basic requirements as laid down by the Canadian authorities.</p>

                            <p><b>A single eligibility evaluation starts at $990. A family eligibility evaluation starts at $1490.</b></p>

                        </div>

                        <div className="worker-cover">
                            <img src={tourist} alt="" />
                        </div>

                        </div>


                </div>

            </div>








            <VisaContact />

           
        </div>

    </Layout>

    );
  }
  
export default TouristVisa;