import React from "react";
import Layout from "../layout/layout";
import VisaContact from "./visacontact";
import workericon from "../../assets/images/visa/worker-icon.svg";
import worker from "../../assets/images/visa/worker.png";
import "./visa.css";



const WorkerVisa = () => {




  
    return (

    <Layout>


        <div className="worker-visa-page">


            <div className="worker-visa-banner">

                <div className="container">

                    <div className="worker-visa-banner-heading">

                        <img src={workericon} alt="" />
                        <div className="worker-visa-banner-heading-title">
                            <h5>Permanent residency</h5>
                            <h2>Skilled Worker Visa</h2>
                        </div>

                    </div>

                    <p>Boasting a standard of living that is the envy of the world, a thriving economy, and unemployment among the lowest in the world, Canada offers a wealth of opportunities to Skilled Workers who wish to immigrate. Skilled Workers – meaning qualified professionals as well as experienced tradespeople – and their families are invited to settle permanently in Canada if their immigration application is successful.</p>



                    <div className="worker-grid">

                        <div className="worker-content">

                            <p>Canada is keen to welcome Skilled Workers, and a wide variety of both Federal and Provincial visa programs exist to facilitate their immigration. These include the Express Entry Program, Federal Skilled Worker Programs, as well as Provincial Nominee Programs. Your UIS personal immigration account manager will help guide you toward the visa program best suited to your professional skill and your desired destination in Canada.</p>

                            <p>In-demand professions currently include those in the fields of banking, insurance, and finance, all aspects of engineering, elementary, high school teachers and lecturers in higher education, medical doctors, nurses, and other health care workers, business people including sales and marketing professionals as well as a whole host of tradespeople, and many many more. Your UIS personal immigration account manager will help you discover if your background makes you eligible for any of these various programs.</p>

                            <p><b>A single eligibility evaluation starts at $990. A family eligibility evaluation starts at $1490.</b></p>

                        </div>

                        <div className="worker-cover">
                            <img src={worker} alt="" />
                        </div>

                        </div>


                </div>

            </div>








            <VisaContact />

           
        </div>

    </Layout>

    );
  }
  
export default WorkerVisa;