import React from "react";
import { Link } from "react-router-dom";
import Layout from "../layout/layout";
import {Form, Button} from 'react-bootstrap';
import logo from "../../assets/images/footer/logo.svg";
import envelope from "../../assets/images/login/envelope.svg";
import call from "../../assets/images/login/call.svg";
import facebook from "../../assets/images/login/facebook.svg";
import linkedin from "../../assets/images/login/linkedin.svg";
import twitter from "../../assets/images/login/twitter.svg";
import "./login.css";
import Helmet from "react-helmet";

const Login = () => {




  
    return (

    <Layout>

        <Helmet>
            <body className="hide-layout" />
        </Helmet>

        <div className="login-page">

        




            <div className="login-grid">


              <div className="login-content">

                  <Link to="/"><img src={logo} alt="" className="logo-login"/></Link>

                  <h2>Welcome!</h2>

                  <div className="login-contact-info">
                    <span><img src={envelope} alt="" />support@uiscanada.com</span>
                    <span><img src={call} alt="" />+49-345 454 33 44</span>
                  </div>

                   <div className="login-social">
                        <ul>
                        <li><Link to="https://www.facebook.com/uis.canada"><img src={facebook} alt="" /></Link></li>
                        <li><Link to="https://www.linkedin.com/company/uis-canada-official/"><img src={linkedin} alt="" /></Link></li>
                        <li><Link to="https://twitter.com/uis_canada"><img src={twitter} alt="" /></Link></li>
                        </ul>
                    </div>
               
              </div>


              <div className="login-form">

                <div className="login-popup-content">

                    <h2>Sign In</h2>
                    <p>Enter your email and password to sign in</p>

                    <Form>

                    <Form.Group>
                        <Form.Control type="email" placeholder="Email Address" />
                    </Form.Group>

                    <Form.Group>
                        <Form.Control type="password" placeholder="Password" />
                    </Form.Group>

                    <div className="forget-section">
                        <div className="remember-me">
                        <Form.Check 
                            type="switch"
                            id="custom-switch"
                            label="Check this switch"
                        />
                        </div>
                        <Link to="/forgot">Forgot Password</Link>
                    </div>

                    <Button type="submit">
                            Sign In
                    </Button>
                    </Form>

                </div>
               
              </div>


            </div>




           
     </div>

    </Layout>
    );
  }
  
export default Login;