import React from "react";
import Layout from "../layout/layout";
import VisaContact from "./visacontact";
import holidayicon from "../../assets/images/visa/holiday-icon.svg";
import holiday from "../../assets/images/visa/holiday.png";
import "./visa.css";



const HolidayVisa = () => {




  
    return (

    <Layout>


        <div className="worker-visa-page">


            <div className="worker-visa-banner">

                <div className="container">

                   


                    <div className="worker-grid">

                        <div className="worker-content">


                        <div className="worker-visa-banner-heading">

                        <img src={holidayicon} alt="" />
                        <div className="worker-visa-banner-heading-title">
                            <h5>Temporary</h5>
                            <h2>Working Holiday Visa</h2>
                        </div>

                        </div>


                            <p>Created as part of the International Experience Canada (IEC) immigration program, a Working Holiday Visa enables young people from the 32 countries with bilateral youth agreements with Canada, who are between the ages 18 and 35, to work and travel in Canada for a period of up to 2 years.</p>

                            <p>Working Holiday Visa holders are able to work for any Canadian employer in any part of the country, and you will not need a specific job offer prior to making your application. On the other hand, not all jobs are open to Working Holiday Visa holders, particularly those which require specific certification such as education, health, and others.</p>

                            <p>Your personal application account manager will guide you through all the requirements of eligibility.</p>

                            <p><b>A single eligibility evaluation starts at $990. A family eligibility evaluation starts at $1490.</b></p>

                        </div>

                        <div className="worker-cover">
                            <img src={holiday} alt="" />
                        </div>

                    </div>


                </div>

            </div>


            <VisaContact />

           
        </div>

    </Layout>

    );
  }
  
export default HolidayVisa;