import React from "react";
import Layout from "../layout/layout";
import aboutbanner from "../../assets/images/about/about-banner.png";
import whoweare from "../../assets/images/about/who-we-are.png";
import rcic from "../../assets/images/about/rcic.svg";
import team from "../../assets/images/about/team.png";
import "./about.css";


const About = () => {




  
    return (

    <Layout>


        <div className="about-page">


            <div className="about-banner">

                <div className="container">

                    <div className="about-banner-grid">


                    <div className="about-banner-cover">

                        <img src={aboutbanner}  alt=""/>

                        <div className="about-bubble cursive-font">
                            <span>“Your life doesn’t get better by chance, it gets better by change”</span>
                        </div>
                    
                    </div>


                    <div className="about-banner-content">

                        <h2>About UIS Canada</h2>

                        <p>Moving to a new country is a big decision! There's a lot to think about, from finding a place to live to getting a job and making new friends. But the hardest part is knowing where to start, which is why many people choose to hire an immigration company like Universal Immigration Services (UIS) Canada.</p>

                        <p>UIS Canada is a private immigration company that specializes in helping people make a fresh start in Canada. In fact, our team of professionals are immigrants themselves, so they know first-hand the challenges you're facing. Whether you're struggling with the language barrier, finding a job, or adjusting to a new culture, we can help. </p>

                        <p>We provide a range of services to support immigrants as they navigate their new lives in Canada. We will help you improve your language skills, search for jobs, fill out paperwork and guide you all the way until successfully completing your immigration process.</p>

                    </div>


                    </div>


                    </div>


            </div>



            <div className="about-experience-section">

                <div className="container">

                    <div className="about-experience">

                        <div className="about-experience-content">

                            <h2>Our Mission</h2>

                            <p>Our mission is to provide efficient and client-focused immigration solutions that enable individuals and families to move to Canada quickly and easily.  </p>

                            <p>We are committed to providing you with the information, resources and support you need to navigate the Canadian immigration system easily and stress-free. </p>

                        </div>

                        <div className="about-experience-cover">
                            <img src={whoweare} alt="" />
                        </div>

                    </div>


                    <div className="about-who-we-are">

                       <div className="about-who-we-are-cover">
                            <img src={whoweare} alt="" />
                        </div>


                        <div className="about-who-we-are-content">

                            <h2>Our Values</h2>

                            <p>We believe that everyone deserves the opportunity to live in a free and democratic country, which is why we work hard to provide a fair and transparent process for our clients. We also believe in the importance of building relationships as a prominent component of our clients’ success.</p>

                            <p>We understand that immigration is a complex issue that entails a difficult journey, and we are committed to helping you navigate the process in a way that is respectful and considerate of your needs. These are just a few of the values that guide our work at UIS Canada.</p>

                        </div>

                      
                    </div>


                </div>

            </div>


            <div className="about-team">

                <div className="container">

                    <div className="about-about-help-content">

                        <h2>Our Team</h2>

                        <p>We are proud to have a team of experienced immigration professionals who are committed to helping our clients achieve their dreams of living and working in Canada. We also exclusively subcontract fully authorized Regulated Canadian Immigration Consultants. These are consultants of good standing, certified by the Immigration Consultants of Canada Regulatory Authority (ICCRC). Please visit our RCIC page for more information.</p>

                        <p>Our staff includes dedicated team members and support representatives who offer a high level of customer service, which is why we are one of the most trusted immigration companies globally. If you’re ready to make a change in your life, contact us today to schedule a free call with one of our company representatives.
                        </p>


                        <div className="team-cover">
                            <img src={team} alt="" />
                        </div>

                    </div>

                 </div>
            </div>


            <div className="about-rcic">

                <div className="container">

                    <div className="about-rcic-grid">

                       <div className="about-rcic-content">

                            <h2>Who is RCIC</h2>

                            <p>UIS Canada exclusively subcontracts fully authorized Regulated Canadian Immigration Consultants. These are consultants of good standing, certified by the Immigration Consultants of Canada Regulatory Authority (ICCRC).</p>

                            <p>UIS subcontracts regulated consultants, to perform eligibility screening of profiles of UIS Canada end-users. This service is provided in accordance with our Terms of Use-authorized representatives.</p>

                        </div>


                        <div className="about-rcic-cover">
                            <img src={rcic} alt="" />
                        </div>

                    </div>

                    <h3>Why Work With A Registered and Authorised Immigration Consultant?</h3>

                    <p>Working with a Regulated Canadian Immigration Consultant gives you the peace of mind of knowing you and your family are in the best possible hands.</p>

                    <p>We take that responsibility very seriously, and it reflects in every aspect of the quality of service that we have provided to our many satisfied clients throughout the years:</p>

                    <ul>
                        <li>It reflects in the expertise and experience we put at your disposal.</li>
                        <li>It reflects in the attention to every smallest detail that can help maximize the success of your application.</li>
                        <li>It reflects in our commitment to providing the very best and most cost effective service to our clients.</li>
                    </ul>

                    <p>Working with UIS Canada means that you have access to one of the country’s leading registered immigration and citizenship consultancies, a consultancy authorized by the Canadian authorities to:</p>

                    <ul>
                        <li>Communicate with the Government of Canada on your behalf</li>
                        <li>Advise you about your immigration options</li>
                        <li>Assist you in completing your program requirements</li>
                    </ul>

                    <p>Represent you in an immigration or citizenship application or hearing</p>

                    <p>Canada awaits you with open arms. Choose UIS Canada to accompany you every step of the way to achieving your Canadian dream.</p>

                </div>

            </div>

           
        </div>

    </Layout>

    );
  }
  
export default About;