import React from "react";
import cover from "../../assets/images/header/consultation-cover.png";
import {Form, Button} from 'react-bootstrap';
import "./modal.css"

const ConsultationPopup = () => {


    return (
        <div className="consultation-popup-grid">

            <div className="consultation-popup-content">

                <h2>Get Free Consultation</h2>
                <p>Leave your details and our team will contact you shortly.</p>

                <Form>
                <Form.Group>
                    <Form.Control type="text" placeholder="Full Name" />
                </Form.Group>

                <Form.Group>
                    <Form.Control type="email" placeholder="Email Address" />
                </Form.Group>

                <Form.Group>
                    <Form.Control type="tel" placeholder="Phone Number" />
                </Form.Group>

                <Button variant="primary" type="submit">
                    Submit
                </Button>
                </Form>
            </div>

            <div className="consultation-popup-cover">
                <img src={cover} alt="" />
            </div>

  
        </div>
    );
  }
  
export default ConsultationPopup;