import React from "react";
import Layout from "../layout/layout";
import VisaContact from "./visacontact";
import familyicon from "../../assets/images/visa/family-icon.svg";
import family from "../../assets/images/visa/family.png";
import "./visa.css";



const FamilyVisa = () => {




  
    return (

    <Layout>


        <div className="worker-visa-page">


            <div className="worker-visa-banner">

                <div className="container">

                   


                    <div className="worker-grid">

                        <div className="worker-content">


                        <div className="worker-visa-banner-heading">

                        <img src={familyicon} alt="" />
                        <div className="worker-visa-banner-heading-title">
                            <h5>Permanent residency</h5>
                            <h2>Family Visa</h2>
                        </div>

                        </div>


                            <p>Family Visas are designed to enable Canadian citizens and permanent residents over the age of 18 to sponsor their immediate family and relatives to join them to live, work, and study in Canada; This includes your spouse or common-law partner, dependent children, parents, and grandparents, orphaned siblings, nephews and nieces, grandsons and granddaughters (under the age of 18 and not married or in common-law partnership), as well children in process of international adoption.</p>

                            <p>The Canadian immigration authorities have created a number of different Family Sponsorship Visa options to enable families to reunite and build a new life in a safe and welcoming country. When successful, family members will then enjoy the social services afforded to all Canadian residents, such as those in the areas of education and healthcare.</p>

                            <p>Your UIS personal immigration account manager is available to answer all your questions regarding the criteria of eligibility and guide you through the intricacies of application for Family Visas.</p>

                            <p><b>A single eligibility evaluation starts at $990. A family eligibility evaluation starts at $1490.</b></p>

                        </div>

                        <div className="worker-cover">
                            <img src={family} alt="" />
                        </div>

                    </div>


                </div>

            </div>


            <VisaContact />

           
        </div>

    </Layout>

    );
  }
  
export default FamilyVisa;