import React from "react";
import Layout from "../layout/layout";
import VisaContact from "./visacontact";
import temporaryicon from "../../assets/images/visa/temporary-icon.svg";
import temporary from "../../assets/images/visa/temporary.png";
import "./visa.css";



const TemporaryVisa = () => {




  
    return (

    <Layout>


        <div className="worker-visa-page">


            <div className="worker-visa-banner">

                <div className="container">

                   


                    <div className="worker-grid">

                        <div className="worker-content">


                        <div className="worker-visa-banner-heading">

                        <img src={temporaryicon} alt="" />
                        <div className="worker-visa-banner-heading-title">
                            <h5>Temporary</h5>
                            <h2>Temporary Visa</h2>
                        </div>

                        </div>


                            <p>The Temporary Visa was designed to enable people to visit Canada for holiday, work, or study for a short period of time for an extended visit but not to work or study.</p>

                            <p>The requirements for eligibility and the possible length of stay are dependent upon each individual’s particular circumstances as well as their country of origin.</p>

                            <p><b>A single eligibility evaluation starts at $990. A family eligibility evaluation starts at $1490.</b></p>

                        </div>

                        <div className="worker-cover">
                            <img src={temporary} alt="" />
                        </div>

                    </div>


                </div>

            </div>


            <VisaContact />

           
        </div>

    </Layout>

    );
  }
  
export default TemporaryVisa;