import React from "react";
import {Form, Button} from 'react-bootstrap';
import Layout from "../layout/layout";
import envelope from "../../assets/images/contact/envelope.svg";
import call from "../../assets/images/contact/call.svg";
import locationicon from "../../assets/images/contact/location.svg";
import locationplace from "../../assets/images/contact/graphic.svg";
import "./contact.css";


const Contact = () => {

  
    return (

    <Layout>


        <div className="contact-page">


            <div className="container">


            <div className="contact-grid">


                <div className="contact-content">

                    <h2>Info</h2>

                    <div className="contact-box">

                        <div className="contact-info-grid">
                            <img src={call} alt="" />
                            <div className="contact-info-text">
                                <span>+1-604-262-3728</span>
                                <span>+1-888-995-2030 (toll free worldwide)</span>
                            </div>
                        </div>

                        <div className="contact-info-grid">
                            <img src={envelope} alt="" />
                            <div className="contact-info-text">
                                <span>support@uiscanada.com</span>
                            </div>
                        </div>

                    </div>





                    <h2>Our Offices</h2>

                    <div className="contact-box">

                        <div className="contact-info-grid">
                            <img src={locationicon} alt="" />
                            <div className="contact-info-text">
                                <span>68 WATER STREET, OFFICE 406, Gastown, Vancouver, BC V6B 1A4</span>
                                <p>Canada</p>
                            </div>
                        </div>

                        <div className="contact-info-grid">
                            <img src={locationicon} alt="" />
                            <div className="contact-info-text">
                                <span>1463 Sofia City, 49 Patriarch Evtimiy Blvd,</span>
                                <p>Bulgaria</p>
                            </div>
                        </div>

                    </div>



                    <div className="location-place">
                        <img src={locationplace} alt="" />
                    </div>
                
                </div>


                <div className="contact-form">

                <div className="contact-form-content">

                    <h2>Contact Us</h2>
                    <p>Leave your details and our proffecionals will contact you soon!</p>

                <Form>
                    <Form.Group>
                        <Form.Control type="text" placeholder="Full Name" />
                    </Form.Group>

                    <Form.Group>
                        <Form.Control type="email" placeholder="Email Address" />
                    </Form.Group>

                    <Form.Group>
                        <Form.Control type="tel" placeholder="Phone Number" />
                    </Form.Group>

                    <Form.Group>
                        <Form.Control as="textarea" rows={3} placeholder="Message"/>
                    </Form.Group>

                    <Button variant="primary" type="submit" >
                        Submit
                    </Button>
                </Form>

                </div>
                
                </div>


            </div>

                   

            </div>

           
        </div>

    </Layout>

    );
  }
  
export default Contact;