import React from "react";
import {Form, Button} from 'react-bootstrap';
import "./visa.css";


const VisaContact = () => {




  
    return (




        <div className="visa-contact">

            <div className="container">

                <div className="visa-contact-content">
                    <h2>Wonder if You are Eligible? Contact Us to Find Out!</h2>
                    <p>Leave your details and our team will contact you shortly.</p>

                    <div className="visa-contact-form">
                            <Form>
                        <Form.Group>
                            <Form.Control type="text" placeholder="Full Name" />
                        </Form.Group>

                        <Form.Group>
                            <Form.Control type="email" placeholder="Email Address" />
                        </Form.Group>

                        <Form.Group>
                            <Form.Control type="tel" placeholder="Phone Number" />
                        </Form.Group>

                        <Button variant="primary" type="submit">
                            Submit
                        </Button>
                        </Form>
                    </div>

                </div>

            
            </div>

        </div>



    );
  }
  
export default VisaContact;