import React, { useState, useEffect } from "react";
import envelope from "../../assets/images/header/envelope.svg";
import envelopewhite from "../../assets/images/header/envelope-white.svg";
import envelopewhitedark from "../../assets/images/header/envelope-white-dark.svg";
import call from "../../assets/images/header/call.svg";
import callwhite from "../../assets/images/header/call-white.svg";
import callwhitedark from "../../assets/images/header/call-white-dark.svg";
import searchicon from "../../assets/images/header/search.svg";
import searchiconlight from "../../assets/images/header/search-light.svg";
import loginicon from "../../assets/images/header/login.svg";
import loginiconwhite from "../../assets/images/header/login-white.svg";
import languageicon from "../../assets/images/header/language.svg";
import languageiconwhite from "../../assets/images/header/language-white.svg";
import logo from "../../assets/images/header/logo.svg";
import logowhite from "../../assets/images/header/logo-white.svg";
import searchlogo from "../../assets/images/header/search-logo.svg";
import searchiconwhite from "../../assets/images/header/search-white.svg";
import closeicon from "../../assets/images/header/close-white.svg";
import menuicon from "../../assets/images/header/menu.svg";
import menuiconwhite from "../../assets/images/header/menu-white.svg";
import searchmbl from "../../assets/images/header/search-mbl.svg";
import searchmblwhite from "../../assets/images/header/search-mbl-white.svg";
import loginmbl from "../../assets/images/header/login-mbl.svg";
import loginmblwhite from "../../assets/images/header/login-mbl-white.svg";
import {Dropdown, Form, Nav, Button, FormControl} from 'react-bootstrap';
import { Link } from "react-router-dom";
import "./header.css";


const Header = ({head}) => {


    const [menuopen, setMenuOpen] = useState(false);

    const [seachpopup, setSeachPopup] = useState(false);


    const toggleMenu = () => {
      setMenuOpen(!menuopen)
    }

    const handleSearch = () => {
      setSeachPopup(!seachpopup)
    }


    useEffect(() => {
      window.addEventListener('scroll', isSticky);
      return () => {
          window.removeEventListener('scroll', isSticky);
      };
  });

         
  
    const isSticky = (e) => {
        const header = document.querySelector('.header');
        const scrollTop = window.scrollY;
        scrollTop >= 150 ? header.classList.add('is-sticky') : header.classList.remove('is-sticky');
    };


  
    return (
        <div className={`header ${head ? `white-header` : ``}`}>

        
  
          <div className="top-header">

              <div className="container">

                <div className="top-header-grid">

                  <div className="contact-info">
                    <span>{head ? <img src={envelopewhite} alt="" /> : <img src={envelope} alt="" />}support@uiscanada.com</span>
                    <span>{head ?<img src={callwhite} alt="" /> : <img src={call} alt="" />}+49-345 454 33 44</span>
                  </div>


                  <div className="auth-info">
                    <div className="search-icon" onClick={handleSearch} role="presentation">{head ? <img src={searchiconlight} alt="" /> : <img src={searchicon} alt="" />}</div>
                    <div className="language-dropdown">
                      <Dropdown>
                        <Dropdown.Toggle variant="success" id="dropdown-basic">
                        {head ?<img src={languageiconwhite} alt="" /> : <img src={languageicon} alt="" /> }
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                          <Dropdown.Item href="#/action-1">English</Dropdown.Item>
                          <Dropdown.Item href="#/action-2">Dutch</Dropdown.Item>
                        </Dropdown.Menu>
                      </Dropdown>
                    </div>
                    <div className="auth-login">
                        <Link to="">Sign In</Link>
                        <Link to="/login">{head ?<img src={loginiconwhite} alt="" /> : <img src={loginicon} alt="" />}</Link>
                    </div>
                  </div>

                </div>

              </div>


          </div>



          <div className={`main-navigation ${menuopen ? `open-menu` : ``}`}>

                <div className="container">

                    <div className="main-navigation-grid">

                      <div className="logo">
                      <Link to="/">{head ?<img src={logowhite} alt="" /> : <img src={logo} alt="" /> }</Link>
                      </div>


                      <div className="mobile-icons">
                         <span onClick={handleSearch} role="presentation">{head ? <img src={searchmblwhite} alt="" />: <img src={searchmbl} alt="" /> }</span> 
                         <span><Link to="/login">{head ? <img src={loginmblwhite} alt="" />: <img src={loginmbl} alt="" /> }</Link></span> 
                      </div>

                      <div className="menu-icon" onClick={toggleMenu}>
                        <span>Menu</span>{head ? <img src={menuiconwhite} alt="" /> : <img src={menuicon} alt="" />  }
                      </div>

                      <div className="menu-grid">
                        <Nav>
                          <Nav.Item>
                            <Link to="/visa/express">Visa Types</Link>
                          </Nav.Item>
                          <Nav.Item>
                            <Link to="/about">About UIS</Link>
                          </Nav.Item>
                          <Nav.Item>
                            <Link to="/faq">FAQ</Link>
                          </Nav.Item>
                          <Nav.Item>
                            <Link to="/blogs">Blogs</Link>
                          </Nav.Item>
                          <Nav.Item>
                            <Link to="/contact">Contact</Link>
                          </Nav.Item>
                        </Nav>

                        <div className="language-mobile">
                          <div className="contact-info">
                            <span><img src={envelopewhitedark} alt="" />support@uiscanada.com</span>
                            <span><img src={callwhitedark} alt="" />+49-345 454 33 44</span>
                          </div>
                          <div className="language-dropdown">
                            <Dropdown>
                              <Dropdown.Toggle variant="success" id="dropdown-basic">
                                <img src={languageiconwhite} alt="" />
                              </Dropdown.Toggle>

                              <Dropdown.Menu>
                                <Dropdown.Item href="#/action-1">English</Dropdown.Item>
                                <Dropdown.Item href="#/action-2">Dutch</Dropdown.Item>
                              </Dropdown.Menu>
                            </Dropdown>
                          </div>
                        </div>
                      </div>

                    </div>

                </div>

         
          </div>



          {seachpopup ? 
            <div className="searc-container">
              <div className="search-content">
                <div className="search-close-btn" onClick={handleSearch} role="presentation">
                  <img src={closeicon} alt="" />
                </div>
                <img src={searchlogo} alt="" />
                <Form>
                  <FormControl
                    type="search"
                    placeholder="Type Word then Press Enter"
                    aria-label="Search"
                  />
                  <Button><img src={searchiconwhite} alt="" /></Button>
                </Form>
              </div>
            </div> : "" }

  
      </div>
    );
  }
  
export default Header;