import React from "react";
import Layout from "../layout/layout";
import VisaContact from "./visacontact";
import expressicon from "../../assets/images/visa/express-icon.svg";
import howwork1 from "../../assets/images/visa/how-work-1.png";
import howwork2 from "../../assets/images/visa/how-work-2.png";
import program from "../../assets/images/visa/program.png";
import "./visa.css";



const Express = () => {




  
    return (

    <Layout>


        <div className="express-visa-page">


            <div className="express-visa-banner">

                <div className="container">

                    <div className="express-visa-banner-heading">

                        <img src={expressicon} alt="" />
                        <div className="express-visa-banner-heading-title">
                            <h5>Permanent residency</h5>
                            <h2>Express Entry</h2>
                        </div>

                    </div>

                    <p>Today, <b>Express Entry</b> is the most sought after pathway for Canadian Visa application.</p>

                    <p>Not a particular visa program itself, Express Entry is the new fast-track system introduced by Immigration, Refugees and Citizenship Canada (IRCC) in January 2015 to enable potential immigrants with desired skills and backgrounds to complete the immigration process in the shortest time possible.</p>

                    <p>The Express Entry process, as well as all the other Canadian Visa application processes, can often seem like an impossible mountain to climb. Here at UIS we’ve ‘been there and done this’ hundreds of times. That’s how we can promise to provide the very best professional expertise and our years of experience to ensure that this complex process will be as simple as possible for you and your family.</p>

                    <p>Your <b>personal immigration account managers will be with you every step</b> of the way to make sure that everything that can help maximize your point score will be taken into account – giving you the best possible opportunity to realize your Canadian dream.</p>


                </div>

            </div>




            <div className="express-how-work">

                <div className="container">

                    <div className="express-how-work-grid">

                        <div className="express-how-work-content">

                            <h3>How it Works</h3>
                            <p>Express Entry is a multi-factored <b>‘pool and points’</b> system that the <b>IRCC</b> uses to select the most suitable candidates for immigration to Canada.</p>

                            <p>To be eligible to enter the Express Entry pool, applications are measured according to a whole range of personal information that is required to be provided. This builds what is called an <b>‘Applicant’s Profile’</b>.</p>

                            <p>To build this profile you will be asked about your educational and/or professional qualifications as well as those of your spouse/partner if relevant, your work experience, language abilities, age, and family situation, if you are being nominated, whether you already have a specific job offer in Canada from a qualifying employer, as well as other factors.</p>

                        </div>

                        <div className="express-how-work-cover">
                            <img src={howwork1} alt="" />
                        </div>

                    </div>




                    <div className="express-how-work-grid how-work-flip">

                        <div className="express-how-work-content">

                            <p>Based on your answers, the system awards points according to what is called the Comprehensive <b>Ranking System (CRS)</b> and your total score will be calculated. The score is calculated out of a possible maximum of 1200.</p>

                            <p>Canada’s immigration authorities then select the most suitable candidates from the pool and offer them an official <b>‘Invitation to Apply’</b> for one of those immigration programs. The higher your score the better your chances of being selected. Once you receive an ITA, you have a maximum 90 day period during which to submit the full online application for permanent residence.</p>

                            <p>The Canadian government through Citizenship and Immigration Canada is committed to processing complete applications within <b>6 months of submission</b>. Candidates can remain in the Express Entry pool for up to 12 months and if they do not receive an invitation within this period, they may submit a new profile.</p>

                        </div>

                        <div className="express-how-work-cover">
                            <img src={howwork2} alt="" />
                        </div>

                    </div>

                </div>

            </div>



            <div className="express-program">

                <div className="container">

                    <div className="express-program-grid">

                        <div className="express-program-content">

                            <h3>Programs Covered by Express Entry</h3>
                            <h4>Express Entry is applicable for the following groups of applicants:</h4>

                            <ul>
                                <li>Federal Skilled Worker Class which includes doctors and engineers etc.</li>
                                <li>Federal Skilled Trades Class designed for welders and plumbers etc.</li>
                                <li>Canadian Experience Class, for people who have already lived, worked, or studied in Canada.</li>
                                <li>Provincial Nominee Programs – these are run by some but not all Provinces.</li>
                            </ul>

                            <p>Once you register with UIS your personal immigration account manager will answer all your questions on this and all the other Canadian Visa options, and begin to guide you towards your exciting new life in Canada.</p>

                            <p><b>A single eligibility evaluation starts at $990. A family eligibility evaluation starts at $1490.</b></p>

                        </div>

                        <div className="express-program-cover">
                            <img src={program} alt="" />
                        </div>

                    </div>
                </div>
            </div>


            <VisaContact />

           
        </div>

    </Layout>

    );
  }
  
export default Express;